/* eslint-disable */
import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewStaff from '@/components/NewStaff'
import ViewStaff from '@/components/ViewStaff'
import EditStaff from '@/components/EditStaff'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-staff',
      component: NewStaff
    },
    {
      path: '/edit/:staff_id',
      name: 'edit-staff',
      component: EditStaff
    },
    {
      path: '/:staff_id',
      name: 'view-staff',
      component: ViewStaff
    }
  ]
})



